import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
class GltfModel {
  constructor(APP) {
    this.APP = APP
    this.gltfLoader = new GLTFLoader()
    this.gltfGroup = new THREE.Group() // GLTF精模组
    this.gltfGroup.name = 'glbGroup'
    this.nameList = []
    this.position = {}
  }

  /**
   *  暴露出去的方法
   *  加载精模
   *  移动位置到相应地点
   *  递归设置模型属性
   */
  loadModel () {
    return new Promise((resolve) => {
      let buildingModel = '/3DModels/cartoon_land.gltf'
      this.gltfLoader.load(buildingModel, (obj) => {
        const sceneGLTF = obj.scene
        this.setCastShadow(sceneGLTF) // 设置可以产生投影
        this.setReceiveShadow(sceneGLTF) // 设置可以接受投影
        resolve(sceneGLTF.children[0])
      })
    })
  }
  // ===================== 阴影相关 =====================
  /**
   * 设置允许引起阴影
   */
  setCastShadow (object) {
    const setCastSh = (obj) => {
      // 允许产生阴影
      obj.castShadow = true
      // 递归往下设置
      if (obj.children.length > 0) {
        obj.children.map(child => {
          setCastSh(child)
        })
      }
    }
    setCastSh(object)
  }

  /**
   * 设置允许接受阴影
   */
  setReceiveShadow (object) {
    const setReceiveSh = (obj) => {
      // 对于mesh允许接受阴影（Object3D和Group不需要）
      obj.receiveShadow = true
      // 防止因接受阴影而导致的物体产生水波纹
      if (obj.material) {
        obj.material.side = THREE.DoubleSide
        obj.material.shadowSide = THREE.BackSide
      }
      // 递归往下设置
      if (obj.children.length > 0) {
        obj.children.map(child => {
          setReceiveSh(child)
        })
      }
    }
    setReceiveSh(object)
  }
}

export default GltfModel
