<template>
  <div>
    <div id="container" ref="threeMapDialog">
    </div>
    <div class="view">
      <div @click="reset">全局视角</div>
      <div :class="index === currentIndex ? 'blue': ''" v-for="(item,index) in list" @click="chooseRoom(index)" :key="index">{{item}}</div>
    </div>
  </div>
</template>

<script>
import Store3D from '../utils/three'
let map = null
export default {
  name: 'Home',
  data () {
    return {
      list: ['岛屿1','岛屿2','岛屿3','岛屿4'],
      currentIndex: null, // 当前选择岛屿
      room0cameraPosition:[67.71837465595135, 1.4524190864826831, 401.9605828165933],
      room0controlTarget:[67.71835315824529, 1.4524009311303818, 401.9604583867656],
      room1cameraPosition:[341.3187824529851, 258.589532381425, 29.96280443327055],
      room1controlTarget:[-134.67617024276637, 1.4524009311304222, -189.90002092587525],
      room2cameraPosition:[679.483074222167, 1.4524009311308381, -147.06661768887224],
      room2controlTarget:[679.483074222166, 1.4524009311303852, -147.0666176888725],
      room3cameraPosition:[9.87853230093765, 212.84370573178734, -500.05727486924764],
      room3controlTarget:[-338.33665224294924, 1.4524009311303825, -519.3413577005416],
      room0Person:{x: 35.396950189773655,y: -112.01401222885976,z: 219.85335656254063},
      room1Person:{x: 272.05169551814424,y: 172.35301065548248,z: -22.821989980502224},
      room2Person:{x: 530.5205816513906,y: -100.545571007452,z: -185.17160492442656},
      room3Person:{x: -45.64095606463366,y: 142.84145836751168,z: -487.8489716493898}
    }
  },
  created() {
  },
  mounted() {
    this.mapInit() 
  },
  methods:{
    mapInit() { // 初始地图
      map = new Store3D()
      map.start()
    },
    chooseRoom(item) { // 选择岛屿
      this.currentIndex = item
      if (item === 0) {
        map.cameraMotion(this.room0cameraPosition[0],this.room0cameraPosition[1],this.room0cameraPosition[2],this.room0controlTarget[0],this.room0controlTarget[1],this.room0controlTarget[2])
        map.setCar(true,this.room0Person)
      }
      if (item === 1) {
        map.cameraMotion(this.room1cameraPosition[0],this.room1cameraPosition[1],this.room1cameraPosition[2],this.room1controlTarget[0],this.room1controlTarget[1],this.room1controlTarget[2])
        map.setCar(true,this.room1Person)
      }
      if (item === 2) {
        map.cameraMotion(this.room2cameraPosition[0],this.room2cameraPosition[1],this.room2cameraPosition[2],this.room2controlTarget[0],this.room2controlTarget[1],this.room2controlTarget[2])
        map.setCar(true,this.room2Person)
      }
      if (item === 3) {
        map.cameraMotion(this.room3cameraPosition[0],this.room3cameraPosition[1],this.room3cameraPosition[2],this.room3controlTarget[0],this.room3controlTarget[1],this.room3controlTarget[2])
        map.setCar(true,this.room3Person)
      }
    },
    reset(){
      this.currentIndex = null
      map.resetEyes() // 重置视角
      map.setCar(false,{x: 0,y: 0,z: 0})
    }
  }
}
</script>

<style scoped>
.view {
  width: 100px;
  height: auto;
  position: fixed;
  right: 20px;
  top: 20px;
  line-height: 40px;
  color: #fff;
  cursor: pointer;
}
.view .blue {
  border: 1px solid;
}
</style>
