import * as THREE from 'three'
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader' // 模型导入
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader' // 模型导入
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js'
class AGVCar {
    constructor(option) {
        option = option || { scale: 0.1 };
        this.scale = option.scale;
    }
    Load = function (curObj, callBack) {
        let mtlLoader = new MTLLoader();//mtl材质加载器
        mtlLoader.load('/3DModels/CartoonMan008.mtl', mtl);//加载.mtl文件，执行mtl函数
        function mtl (material) {
            var objLoader = new OBJLoader();//obj模型加载器
            objLoader.setMaterials(material);//mtl材质赋值给obj模型
            objLoader.load('/3DModels/CartoonMan008.obj', obj);//加载.obj文件，执行obj函数 }
            function obj (object3D) {
                object3D.scale.set(1, 1, 1);//放大object3D对象
                object3D.traverse(function (child) {
                    if (child instanceof THREE.Mesh) {
                        child.geometry.applyMatrix(new THREE.Matrix4().makeRotationY(Math.PI / 2));
                        child.material.transparent = true;
                    }
                });
                curObj[callBack](object3D);
            }
        }
    }
    LoadFBX = function (curObj, callBack) {
        let fbxLoader = new FBXLoader();//mtl材质加载器
        fbxLoader.load('/3DModels/Standard Idle.fbx', mtl);//加载.mtl文件，执行mtl函数
        function mtl (material) {
            material.scale.set(0.2, 0.2, 0.2);//放大object3D对象
            material.traverse(function (child) {
                if (child instanceof THREE.Mesh) {
                    child.geometry.applyMatrix(new THREE.Matrix4().makeRotationY(Math.PI / 2));
                    child.material.transparent = true;
                }
            });
            curObj[callBack](material);
        }
    }
}
export default AGVCar
